function translateArrToTree(arr: TreeItem[]) {
  // 1.å…ˆæ˜¯åˆ¶ä½œå­—å…¸èƒ½å¤ŸèŽ·å¾—æ¯ä¸€è¡Œçš„ä¿¡æ¯
  const newArr: TreeItem[] = []
  const map = new Map()
  arr.forEach((item: TreeItem) => {
    // ä¸ºäº†æ–¹ä¾¿ç»™æ¯ä¸€é¡¹éƒ½æ·»åŠ ä¸Šchildren
    // å¯¹è±¡æ˜¯ç›´æŽ¥.å°±å¯ä»¥æ·»åŠ å±žæ€§çš„
    item.children = []
    const key = item.id
    map.set(key, item)
  })

  // 2.éåŽ†æ¯ä¸€é¡¹,ç„¶åŽæœ‰çˆ¶çº§çš„æ·»åŠ åˆ°çˆ¶çº§çš„childrenä¸­,æ²¡æœ‰çˆ¶çº§çš„ç›´æŽ¥æ·»åŠ åˆ°æ–°çš„æ•°ç»„ä¸­
  arr.forEach((item: TreeItem) => {
    const parent = map.get(item.pid)
    if (parent) {
      parent.children.push(item)
    } else {
      newArr.push(item)
    }
  })
  return newArr
}
